body {
  background: rgb(131, 58, 180);
  background: linear-gradient(
    90deg,
    rgba(131, 58, 180, 1) 0%,
    rgba(253, 29, 29, 1) 50%,
    rgba(252, 176, 69, 1) 100%
  );
}

.app-content {
  text-align: center;
}
.App header {
  color: white;
  font-family: "Courier New", Courier, monospace;
}
.App img {
  width: 200px;
  border-radius: 50%;
  margin-top: 10px;
}
